html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e7eaf6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-container {
  margin: 30px auto 0 auto;
  width: 100%;
  text-align: center;
}

.header-title {
  font-size: 40px;
  font-weight: bold;
  color: #113f67;
  box-sizing: border-box;
}

.header-navigation-dropdown {
  margin: 15px;
}

.header-navigation-dropdown span {
  font-size: 18px;
}

.dropdown {
  display: inline-block;
  width: 200px;
  margin: 0 10px;
  z-index: 999;
}

.homepage-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: 20px;
  width: 100%;
  max-width: 700px;
  padding: 20px;
  border-bottom: solid 1px rgba(0, 0, 0, 0.3);
  text-align: justify;
  box-sizing: border-box;
}

.homepage-container span {
  font-weight: bold;
  color: #113f67;
}

.homepage-container .poetic-section {
  font-family: 'Courier New', Courier, monospace;
  text-align: left;
  font-size: 24px;
  color: #113f67;
  font-style: italic;
  margin: 0 auto;
}

.homepage-container hr {
  color: #38598b;
  opacity: 0.1;
  width: 100%;
  margin-top: 20px;
}

.navigation-container {
  text-align: center;
}

.navigation-container .lets-start-button {
  padding: 15px 20px;
  background: #113f67; /*Button Color*/
  color: white;
  font-size: 18px;
  border-radius: 5px;
  border: none;
  outline: none;
  cursor: pointer;
}

.navigation-container .lets-start-button:active {
  -webkit-transform: translateY(4px);
  transform: translateY(4px);
  border-bottom: none;
}

.subheader-container {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 20px 0;
  width: 100%;
}

.subheader {
  text-align: center;
  color: #113f67;
  border: 3px dashed #38598b;
  font-size: 24px;
  font-weight: 500;
  line-height: 24px;
  border-radius: 5px;
  width: auto;
  padding: 10px 20px;
  margin: 0px 20px;
  z-index: 1;
  max-width: 350px;
}

hr {
  margin-left: 0;
  margin-right: 0;
  width: 30%;
  color: #38598b;
  opacity: 0.7;
}

@media only screen and (max-width: 468px) {
  .subheader {
    font-size: 18px;
  }

  hr {
    display: none;
  }
}

.post-container {
  display: -webkit-flex;
  display: flex;
  /* width: 700px; */
  width: 100%;
  max-width: 1000px;
  padding: 20px;
  border-bottom: solid 1px rgba(0, 0, 0, 0.3);
  box-sizing: border-box;
}

.horizontal-image {
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.vertical-image {
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}

.vertical-image img,
.horizontal-image img {
  border-radius: 8px;
}

.vertical-image img {
  /* height: 700px; */
  box-sizing: border-box;
}
.horizontal-image img {
  min-height: 400px;
  /* width: 100%; */
  box-sizing: border-box;
}

.description {
  font-size: 18px;
  color: #113f67;
  text-align: justify;
}

.vertical-image .description {
  box-sizing: border-box;
  padding: 0 20px;
}

.horizontal-image .description {
  box-sizing: border-box;
  padding: 20px 20px 0 20px;
}

.image-container .no-image {
  padding: 0 20px;
}

@media only screen and (max-width: 710px) {
  .vertical-image,
  .horizontal-image {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    height: auto;
    box-sizing: border-box;
  }

  .vertical-image img {
    width: 75%;
    height: auto;
  }
  .vertical-image .description {
    padding-top: 20px;
  }
  .horizontal-image img {
    min-height: 0px;
    width: 100%;
  }

  /* img, .description {
    max-width: 90vw;
  } */
}

.navigation-controls {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  max-width: 700px;
  margin: 30px auto;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.navigation-controls div {
  font-size: 20px;
  color: #113f67;
  cursor: pointer;
}

.navigation-controls div:hover {
  text-decoration: underline;
}

.navigation-controls .left {
  width: 50%;
  text-align: left;
}

.navigation-controls .right {
  width: 50%;
  text-align: right;
}

.semester-container,
.post-grid {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
}

.spinner-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 500px;
  width: 100%;
}

.video-container {
  text-align: center;
}

.video-container h3 {
  color: #113f67;
}

@media only screen and (max-width: 710px) {
  .video-container {
    box-sizing: border-box;
  }

  .video iframe {
    width: 100% !important;
  }
}

.App {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  min-height: 100%;
  box-sizing: border-box;
  margin: 0 auto;
  font-family: "Work Sans", "Roboto", sans-serif;
}

